import React, {Component} from "react";
import Slide from "react-reveal";

class Resume extends Component {
    getRandomColor() {
        let letters = "0123456789ABCDEF";
        let color = "#";
        for (let i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    }

    render() {
        if (!this.props.data) return null;

        const skillmessage = this.props.data.skillmessage;
        const education = this.props.data.education.map(function (education) {
            return (
                <div key={education.school}>
                    <h3>{education.school}</h3>
                    <p className="info">
                        {education.degree} <span>&bull;</span>
                        <em className="date">{education.graduated}</em>
                    </p>
                    <p>{education.description}</p>
                </div>
            );
        });

        const work = this.props.data.work.map(function (work) {
            return (
                <div key={work.company}>
                    <h3>{work.company}</h3>
                    <p className="info">
                        {work.title}
                        <span>&bull;</span> <em className="date">{work.years}</em>
                    </p>
                    <p>{work.description}</p>
                </div>
            );
        });

        const skills = this.props.data.skills.map((skills) => {
            const backgroundColor = this.getRandomColor();
            const className = "bar-expand " + skills.name.toLowerCase();
            const width = skills.level;

            return (
                <li key={skills.name}>
                    <span style={{width, backgroundColor}} className={className}></span>
                    <em>{skills.name}</em>
                </li>
            );
        });

        return (
            <section id="resume" className="column-flex-center">
                <Slide left duration={1300}>
                    <div className=" column-flex-center-left-fix">
                        <div className="marg-name">
                            <h1>
                                <span>Web Development</span>
                            </h1>
                        </div>

                        <div>
                            Whether you need a website for your business or e-commerce venture, I can help you
                            create a modern and effective online presence.
                            From wireframes to mockups and coding, I can handle every step of the web development
                            process, ensuring that your site is optimized
                            and ready to launch. However, if you want to focus on running your business and leave
                            the technical details to a professional,
                            I'm happy to take on the task and deliver a website that meets your requirements and
                            reflects your brand.
                        </div>
                    </div>
                </Slide>

                <Slide left duration={1300}>
                    <div className=" column-flex-center-left-fix">
                        <div className="marg-name">
                            <h1>
                                <span>
                        Automation, Bots
                    </span>
                            </h1>
                        </div>

                        <div>
                            Tired of performing repetitive tasks day in and day out? I can help you work smarter by
                            creating custom scripts and bots that automate your workflow. Whether you're an individual
                            or a business, these solutions can save you time and boost your productivity. From simple
                            tasks to complex processes, I can create a solution that meets your specific needs and
                            streamlines your workflow. Let me help you focus on what matters most, while I take care of
                            the technical details.
                        </div>
                    </div>
                </Slide>

                <Slide left duration={1300}>
                    <div className=" column-flex-center-left-fix">
                        <div className="marg-name">
                            <h1>
                                <span>Backend Development</span>
                            </h1>
                        </div>

                        <div>
                            If you need application development, I can help you leverage advanced technologies such as
                            REST, gRPC, SOAP, and WebSocket. With these technologies, you can create highly efficient
                            and scalable applications that support fast and reliable data exchange between clients and
                            servers. I can develop applications that best meet your needs and your business
                            requirements. Let me help you create applications that maximize your potential and
                            streamline your business operations.
                        </div>
                    </div>
                </Slide>

                {/*<Slide left duration={1300}>*/}
                {/*    <div className="row work">*/}
                {/*        <div className="three columns header-col">*/}
                {/*            <h1>*/}
                {/*                <span>Work</span>*/}
                {/*            </h1>*/}
                {/*        </div>*/}

                {/*        <div className="nine columns main-col">{work}</div>*/}
                {/*    </div>*/}
                {/*</Slide>*/}

                {/*<Slide left duration={1300}>*/}
                {/*    <div className="row skill">*/}
                {/*        <div className="three columns header-col">*/}
                {/*            <h1>*/}
                {/*                <span>Skills</span>*/}
                {/*            </h1>*/}
                {/*        </div>*/}

                {/*        <div className="nine columns main-col">*/}
                {/*            <p>{skillmessage}</p>*/}

                {/*            <div className="bars">*/}
                {/*                <ul className="skills">{skills}</ul>*/}
                {/*            </div>*/}
                {/*        </div>*/}
                {/*    </div>*/}
                {/*</Slide>*/}
            </section>
        );
    }
}

export default Resume;
